@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  background-color: #f2f2f2;
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: white;
  box-shadow: 0px 2px 20px -10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  max-width: 700px;
  min-height: 100vh;
  padding: 10vh 20px;
}

@media only screen and (min-width: 768px) {
  .App {
    margin: 0 auto;
    padding: 10vh 40px;
  }
}

.App h1 {
  margin-bottom: 2rem;
  text-align: center;
}

.App h2 {
  margin-bottom: 0.5em;
}

fieldset {
  margin-bottom: 1.5rem !important;
}

.MuiButton-fullWidth {
  display: block !important;
  margin: 0 auto !important;
  max-width: 250px !important;
}

.MuiButton-root.btn {
  min-width: 150px;
}

.MuiButton-root.btn-small {
  min-width: 110px;
}

.MuiContainer-root.wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5rem;
}

.MuiContainer-root.row {
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 768px) {
  .MuiContainer-root.row {
    flex-direction: row;
    justify-content: space-between;
  }

  .MuiContainer-root.row.order {
    flex-direction: row;
    justify-content: start;
  }
}

.MuiContainer-root.row.btn-group {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 3rem;
}

.MuiContainer-root.confirm-input {
  align-items: baseline;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.MuiContainer-root.confirm-input > *:first-child {
  flex: 1 1;
  margin-bottom: 1rem;
  margin-right: 1rem;
  min-width: 100%;
}

@media only screen and (min-width: 768px) {
  .MuiContainer-root.confirm-input > *:first-child {
    min-width: unset;
  }
}

.MuiContainer-root.result-group {
  margin-top: 2rem;
}

.MuiContainer-root.result-group > *:first-child {
  font-weight: bold;
}

.MuiContainer-root.result-group > *:first-child:only-child {
  display: none;
}

.MuiContainer-root.result {
  margin-bottom: 2rem;
}

.MuiTypography-root.result-txt {
  display: flex;
  align-items: center;
}

.MuiTypography-root.result-txt > strong {
  margin-left: auto;
}

